html {
  scroll-behavior: smooth;
}

*, *::before, *::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  line-height: 1.5;
  background-color: hsl(226deg, 43%, 10%);
  font-family: "Rubik", sans-serif;
  padding: 15vh 0;
}

img, svg, picture, svg {
  display: block;
  max-width: 100%;
}

ul, ol {
  list-style-type: none;
}

input, button, textarea, select {
  font: inherit;
}

h1, h2, h3 {
  font-weight: 500;
}

h2 {
  font-size: 1.125rem;
}

a, a:visited, a:active {
  text-decoration: none;
}

.container {
  max-width: 70.875rem;
  padding: 0 1.5rem;
  margin: 0 auto;
}

.dashboard {
  color: hsl(0deg, 0%, 100%);
}
.dashboard__wrapper {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
  grid-template-areas: "main" "work" "play" "study" "exercise" "social" "self-care";
}
@media (min-width: 40rem) {
  .dashboard__wrapper {
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: "main main" "work play" "study exercise" "social self-care";
  }
}
@media (min-width: 59.375rem) {
  .dashboard__wrapper {
    grid-template-columns: repeat(4, 1fr);
    grid-template-areas: "main work play study" "main exercise social self-care";
  }
}
.dashboard__card--main {
  border-radius: 0.9375rem;
  background-color: hsl(236deg, 45%, 20%);
  grid-area: main;
}
.dashboard__card--small {
  border-radius: 0.9375rem 0.9375rem 1.25rem 1.25rem;
  padding-top: 2.375rem;
  background-repeat: no-repeat;
  background-position: top -0.3125rem right 1.25rem;
}
@media (min-width: 59.375rem) {
  .dashboard__card--small {
    padding-top: 2.8125rem;
  }
}
.dashboard__card--small[data-type=work] {
  background-color: hsl(15deg, 100%, 70%);
  background-image: url("/images/icon-work.svg");
  grid-area: work;
}
.dashboard__card--small[data-type=play] {
  background-color: hsl(195deg, 74%, 62%);
  background-image: url("/images/icon-play.svg");
  grid-area: play;
}
.dashboard__card--small[data-type=study] {
  background-color: hsl(348deg, 100%, 68%);
  background-image: url("/images/icon-study.svg");
  grid-area: study;
}
.dashboard__card--small[data-type=exercise] {
  background-color: hsl(145deg, 58%, 55%);
  background-image: url("/images/icon-exercise.svg");
  grid-area: exercise;
}
.dashboard__card--small[data-type=social] {
  background-color: hsl(264deg, 64%, 52%);
  background-image: url("/images/icon-social.svg");
  grid-area: social;
}
.dashboard__card--small[data-type=self-care] {
  background-color: hsl(43deg, 84%, 65%);
  background-image: url("/images/icon-self-care.svg");
  grid-area: self-care;
}
.dashboard__card-user-info {
  background-color: hsl(246deg, 80%, 60%);
  padding: 2.1875rem 2rem;
  border-radius: 0.9375rem;
  display: flex;
  align-items: center;
  justify-content: start;
  gap: 1.25rem;
  font-size: 0.9375rem;
  color: hsl(236deg, 100%, 87%);
}
@media (min-width: 59.375rem) {
  .dashboard__card-user-info {
    align-items: flex-start;
    flex-direction: column;
    padding: 2.3125rem 2rem 5rem;
    gap: 2.6875rem;
  }
}
.dashboard__card-user-photo {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  border: 0.1875rem solid hsl(0deg, 0%, 100%);
}
@media (min-width: 59.375rem) {
  .dashboard__card-user-photo {
    width: 4.875rem;
    height: 4.875rem;
  }
}
.dashboard__card-username {
  font-size: 1.5rem;
  font-weight: 300;
  color: hsl(0deg, 0%, 100%);
}
@media (min-width: 59.375rem) {
  .dashboard__card-username {
    font-size: 2.5rem;
  }
}
.dashboard__card-controls {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 1.5625rem;
  font-size: 1.125rem;
}
@media (min-width: 59.375rem) {
  .dashboard__card-controls {
    flex-direction: column;
    align-items: flex-start;
    gap: 1.3125rem;
  }
}
.dashboard__card-control {
  all: unset;
  color: hsl(235deg, 45%, 61%);
  cursor: pointer;
}
.dashboard__card-control[data-active] {
  color: hsl(0deg, 0%, 100%);
}
.dashboard__card-content {
  background-color: hsl(236deg, 45%, 20%);
  border-radius: 0.9375rem;
  padding: 1.75rem 1.5rem;
}
@media (min-width: 59.375rem) {
  .dashboard__card-content {
    padding: 2rem 1.875rem;
  }
}
.dashboard__card-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.4375rem;
}
@media (min-width: 59.375rem) {
  .dashboard__card-header {
    margin-bottom: 1.5rem;
  }
}
.dashboard__card-button {
  all: unset;
  cursor: pointer;
  justify-self: end;
}
.dashboard__card-time {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 59.375rem) {
  .dashboard__card-time {
    display: block;
  }
}
.dashboard__card-current-time {
  font-weight: 300;
  font-size: 2rem;
}
@media (min-width: 59.375rem) {
  .dashboard__card-current-time {
    font-size: 3.5rem;
  }
}
.dashboard__card-previous-time {
  font-size: 0.9375rem;
  color: hsl(236deg, 100%, 87%);
}